export default {
	props: {
		// 加载中
		requesting: {
			type: Boolean,
			value: false
		},
		// 加载完毕
		end: {
			type: Boolean,
			value: false,
		},
		// 控制空状态的显示
		emptyShow: {
			type: Boolean,
			value: false,
		},
		// 当前列表长度
		listCount: {
			type: Number,
			default(){
				return 0
			}
		},
		// 空状态的图片
		emptyUrl: {
			type: String,
			default(){
				return "/static/scroll/image/empty/empty.png"
			}
		},
		// 空状态的文字提示
		emptyText: {
			type: String,
			default(){
				return "未找到数据"
			} 
		},
		// 是否有header
		hasTop: {
			type: Boolean,
			value: false
		},
		// 下拉刷新的高度
		refreshSize: {
			type: Number,
			default(){
				return 90
			}
		},
		// 底部高度
		bottomSize: {
			type: Number,
			default(){
				return 0
			}
		},
		// 颜色
		color: {
			type: String,
			default(){
				return "#3F82FD"
			}
		},
	},
	watch: {
		requesting(newVal, oldVal){
			this.requestingEnd(newVal, oldVal);
		},
		listCount(newVal, oldVal){
			if(newVal > 0){
				this.$nextTick(() => {
					this.isFullScreen();
				});
			}
		}
	},
	data() {
		return {
			mode: 'refresh', // refresh 和 more 两种模式
			successShow: false, // 显示success
			successTran: false, // 过度success
			refreshStatus: 0, // 1: 下拉刷新, 2: 松开刷新, 3: 加载中, 4: 加载完成
			move: -45, // movable-view 偏移量
			scrollHeight1: 0, // refresh view 高度负值
			scrollHeight2: 0, // refresh view - success view 高度负值
			scrollTop: 0,
			oldX: 0,
		}
	},
	mounted() {
		this.initScroll();
	},
	methods: {
		/**
		 * 处理 bindscrolltolower 失效情况
		 */
		scroll(e) {
			clearTimeout(this.timer)
			this.timer = setTimeout(() => {
				this.scrollTop = e.detail.scrollTop;
			}, 100)
		},
		/**
		 * movable-view 滚动监听
		 */
		change(e) {
			const refreshStatus = this.refreshStatus;

			// 判断如果状态大于3则返回
			if (refreshStatus >= 3) {
				return;
			}

			let diff = e.detail.y;

			if (diff > -10) {
				this.refreshStatus = 2;
			} else {
				this.oldX = diff;
				this.refreshStatus = 1;
			}
		},
		/**
		 * movable-view 触摸结束事件
		 */
		touchend(e) {
			const refreshStatus = this.refreshStatus;

			if (refreshStatus >= 3) {
				return;
			}

			if (refreshStatus == 2) {
				wx.vibrateShort();
				this.refreshStatus = 3;
				this.move = 0;
				this.mode = 'refresh';
				this.$emit('refresh');
			} else if (refreshStatus == 1) {
				this.move = this.oldX;
				this.$nextTick(() => {
					this.move = this.scrollHeight1;
				})
			}
		},
		/**
		 * 加载更多
		 */
		more() {
			if (!this.end) {
				this.mode = 'more';
				this.$emit('more');
			}
		},
		/**
		 * 监听 requesting 字段变化, 来处理下拉刷新对应的状态变化
		 */
		requestingEnd(newVal, oldVal) {
			if (this.mode == 'more') {
				return
			}

			if (oldVal === true && newVal === false) {
				this.refreshStatus = 4;
				this.move = this.scrollHeight2;

				setTimeout(() => {
					this.successShow = true;
					setTimeout(() => {
						this.successTran = true;
						this.move = this.scrollHeight1;
						setTimeout(() => {
							this.refreshStatus = 1;
							this.successShow = false;
							this.successTran = false;
							this.move = this.scrollHeight1;
						}, 350);
					}, 1000);
				}, 650);

			} else {
				if (this.refreshStatus != 3) {
					this.refreshStatus = 3;
					this.move = 0;
				}
			}
		},
		/**
		 * 计算内容高度，有没有满屏，没有满屏的话自动加载下一页
		 */
		isFullScreen() {
			let query = this.createSelectorQuery();
			let _this = this;

			query.select("#movableview").boundingClientRect();
			query.select("#container").boundingClientRect();

			query.exec(function (res) {
				if(res[0].height > res[1].height){
					_this.more();
				}
			});
		},
		
		/**
		 * 初始化scroll组件参数, 动态获取 下拉刷新区域 和 success 的高度
		 */
		initScroll() {
			let query = this.createSelectorQuery();
			let _this = this;

			query.select("#refresh").boundingClientRect();
			query.select("#success").boundingClientRect();

			query.exec(function (res) {
				_this.scrollHeight1 = -res[0].height;
				_this.scrollHeight2 = res[1].height - res[0].height;
				_this.move = -res[0].height;
			});
		}
	}
}
